import auth from '../tools/storage.js';

const listProfile = document.querySelector('#list-profile');

const prepareProfileMarkup = () => `
                    <table >
                       <tr>
                          <th><h3> :</h3></th>
                          <th> </th> 
                       
                        </tr>
                        <tr>
                           <td><span>:</span></td>
                            <td><span>${auth.data.nickname}</span></td>
                        </tr>
                         <tr>
                           <td><span> :</span></td>
                            <td><span>${auth.data.lastLogin}</span></td>
                        </tr>
                         <tr>
                           <td><span>:</span></td>
                            <td><span>${auth.data.directory}</span></td>
                        </tr>
                         <tr>
                           <td><span>:</span></td>
                            <td><span>${auth.data.email}</span></td>
                        </tr>
                         <tr>
                           <td><span>:</span></td>
                            <td><span> </span></td>
                        </tr>
                    </table>`;

export default {
  setProfile() {
    listProfile.innerHTML = (auth.isSetFlag) ? prepareProfileMarkup() : '';
  },

  clearProfile() {
    listProfile.innerHTML = '';
  }
};

/*

  <div id="profile" class="card p-3 w-50 text-dark">
    <h3> </h3>
    <p><span>: </span><span>${auth.data.nickname}</span></p>
    <p><span>  : </span><span>${auth.data.lastLogin}</span></p>
    <p><span></span>: <span>${auth.data.directory}</span></p>
    <p><span></span>: <span>${auth.data.email}</span></p>
  </div>
  */



